* {

   -webkit-box-sizing: border-box;

    box-sizing: border-box;

}

/*
 * 禁止文本缩放 字体设置  取消touch高亮效果
 */

html {

    width: 100%;

    height: 100%;

   margin: 0px;

   padding: 0px;

   -webkit-text-size-adjust: 100%;

   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

   font-size: 12px;

    background: transparent;

   }

body {

    width: 100%;

    height: 100%;
    
    font-family: -apple-system, "PingFang SC","Helvetica Neue", Helvetica, STHeiTi, sans-serif,"Segoe UI","Lucida Grande",Arial,"Microsoft YaHei",FreeSans,Arimo,"Droid Sans","wenquanyi micro hei","Hiragino Sans GB","Hiragino Sans GB W3",FontAwesome;

   -webkit-overflow-scrolling: touch;
   
   overflow-x: hidden;
   

 }

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, sumary {

    margin: 0;

    padding: 0;

}

/*
 * 清除输入框内阴影
 */

input, select,textarea {

border: none;

-moz-appearance:none;

     appearance:none;

-webkit-appearance: none;

outline: none;

resize: none;

-webkit-box-sizing: border-box;

        box-sizing: border-box;

border-radius: 0;

}

input[type=number] {  
    -moz-appearance:textfield;  
}

input[type=number]::-webkit-inner-spin-button,  
input[type=number]::-webkit-outer-spin-button {  
    -webkit-appearance: none;  
    margin: 0;  
}

ol, ul {

    list-style: none;

}

/*
 * 禁止选中文本内容
 */

/**:not(input, select, textArea) {

    -webkit-user-select: none;

}*/

/*
 * 禁用长按页面时的弹出菜单(iOS下有效) ,img和a标签都要加
 */

img,a{

-webkit-touch-callout:none;

}

a,a:active,a:hover {

text-decoration: none;

}

/*
 * 去掉点击链接和文本框对象时默认的灰色半透明覆盖层(iOS)或者虚框(Android)
 */

a,button,input,textarea{ 

-webkit-tap-highlight-color:rgba(0,0,0,0);

}

