  -moz-appearance: checkbox;
       appearance: checkbox;
  -webkit-appearance: checkbox;
  width: 14px;
  height: 14px;
  margin-right: 24px;
      float: left;
    margin-top: 23px;
}

.shopping_cart_list .top_title span{
