}

.shopping_cart_list .list .check_btn{
  -moz-appearance: checkbox;
       appearance: checkbox;
  -webkit-appearance: checkbox;
  width: 14px;
  height: 14px;
      float: left;
    margin-top: 38px;
    margin-right: 24px;
}

.shopping_cart_list .list .img_box{
  width: 180px;
  float: left;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow: hidden;
  height: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.shopping_cart_list .list .img_box img{
      width: 70px;
  float: left;
}

.shopping_cart_list .list .name_box{
   width: 360px;
   float: left;
   font-size: 18px;
   height: 90px;
   overflow: hidden;
   padding: 22px 0;
}

.shopping_cart_list .list .name_box .merchandise_name{
  float: left;
  width: 100%;
  height: 30px;
  line-height: 30px;
}

.shopping_cart_list .list .no_type .merchandise_name{
  height: 46px;
  line-height: 46px;
}

.shopping_cart_list .list .name_box a{
   color: #000;
   font-family: "\5FAE\8F6F\96C5\9ED1";
}

.shopping_cart_list .list .name_box .type{
  font-size: 12px;
  display: block;
  float: left;
  height: 20px;
  line-height: 20px;
