  font-weight: 900;
}

.shopping_cart_list .list .unit_price{
    width: 100px;
    text-align: right;
    font-size: 18px;
        float: left;
    height: 90px;
    line-height: 90px;
}

.shopping_cart_list .list .numbers{
    width: 240px;
    text-align: center;
    float: left;
        height: 90px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.shopping_cart_list .list .subtotal{
      width: 100px;
      text-align: right;
      float: left;
      height: 90px;
      line-height: 90px;
      font-size: 18px;
      color: #ff6700;
}

.shopping_cart_list .list .operation{
      width: 140px;
    text-align: center;
    float: left;
    height: 90px;
      line-height: 90px;
      font-size: 18px;
      position: relative;
}

.shopping_cart_list .list .operation .del{
  position: absolute;
  z-index: 1000;
  width: 30px;
  height: 30px;
  left: calc(50% - 15px);
  top: calc(50% - 15px);
  line-height: 30px;
  color: #757575;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.shopping_cart_list .list .operation .del:hover + .circular{
  opacity: 1;
}

.shopping_cart_list .list .operation .del:hover{
  color: #fff;
}

.shopping_cart_list .list .operation .circular{
  background: #e53935;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  position: absolute;
  left: calc(50% - 15px);
  top: calc(50% - 15px);
  z-index: 0;
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.shopping_cart_list .statistics_box{
  width: 1200px;
  background: #fff;
  height: 50px;
  line-height: 50px;
