  height: 50px;
  background: #ff6700;
  color: #fff;
  font-size: 18px;
  line-height: 50px;
  margin-left: 50px;
  border:none;
  cursor: pointer;
}

.shopping_cart_list .statistics_box .settlement[disabled]{
  background: #99918b;
  cursor: not-allowed;
}

.shopping_cart_list .hint{
  width: 1200px;
  height: 400px;
  text-align: center;
  font-size: 40px;
  line-height: 400px;
  border-radius: 5px;
  border:1px dashed #ccc;
  color: #ccc;
}

.numbers_box{
  overflow: hidden;
  height: 40px;
  line-height: 40px;
  border: 1px solid #e0e0e0;
  font-size: 18px;
}

.numbers_box .decrease{
  width: 40px;
  height: 40px;
  line-height: 40px;
  float: left;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.numbers_box .decrease:hover{
  background: #ccc;
}

.numbers_box .add{
  width: 40px;
  height: 40px;
  line-height: 40px;
  float: right;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.numbers_box .add:hover{
  background: #ccc;
}

.numbers_box .number_put{
  float: left;
  height: 36px;
  width: 60px;
  text-align: center;
  font-size: 18px;
}

.clearFix:after{
  clear: both;
  display: block;
  content: '';
}

